
import { computed, ComputedRef, defineComponent, Ref } from 'vue';
import { IonPage, IonContent, IonIcon, IonHeader, IonToolbar, IonToggle, IonTitle } from '@ionic/vue';
import { useStore } from 'vuex';
import User from '@/store/modules/auth/models/User';

interface Option {
  title: string
  subCategories: Array<{
    name: string
    isDangerous: boolean
    toggleValue?: boolean
    action?: () => void
  }>
}

export default defineComponent({
    name: "Settings",
    
    components: { IonPage, IonContent, IonIcon, IonHeader, IonToolbar, IonToggle, IonTitle },
    setup() {
        const store = useStore()
        const isLoading: ComputedRef<boolean> = computed(() => store.getters.isLoading)
        const user: Ref<User> = computed(() => store.getters.user)
        const optionsList: Option[] = [
            {
                title: "Account",
                subCategories: [
                    {
                        name: "Limits and features",
                        isDangerous: false
                    },
                    {
                        name: "Native currency",
                        isDangerous: false
                    },
                    {
                        name: "Privacy",
                        isDangerous: false
                    },
                    {
                        name: "Notification settings",
                        isDangerous: false
                    },
                ]
            },
            {
                title: "Security",
                subCategories: [
                    {
                        name: "Require PIN / Biometrics",
                        isDangerous: false,
                        toggleValue: false
                    },
                    {
                        name: "Privacy mode",
                        isDangerous: false,
                        toggleValue: true
                    },
                    {
                        name: "Sign out",
                        isDangerous: true,
                        action: () => store.dispatch('signUserOut'),
                    }
                ]
            }
        ]
        

        return { isLoading, user, optionsList }
    }
})
